@import url(https://use.typekit.net/izw3hmc.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/*#region  CSS-Setup */

/*? ===  font(s) === */

/* futura: font-family: futura-pt,sans-serif; */

/* #region setup & themes  */

:root {

  overflow-wrap: break-word;
  word-wrap: break-word;

  --font-futura: futura-pt,sans-serif;
  font-weight: 400;
  font-style: normal; 

  --fs-400: 1rem;
  --fs-500: 1.125rem;
  --fs-600: 1.375rem;
  --fs-700: 2rem;

  /* ! test that out if it is working */
  @media (min-width: 45em){
  --fs-400: 1.125rem;
  --fs-500: 1.375rem;
  --fs-600: 2.75rem;
  --fs-700: 4.125rem;
  }

  /* ==================  
?    ===  Colour-Palette  ===
.     ================== */
  --wkd-black: rgb(0, 0, 0);
  --wkd-darkgrey: rgb(29, 29, 29);
  --wkd-midgrey: rgb(61, 61, 61);
  --wkd-grey: rgb(107, 106, 106);
  --wkd-lightgrey: rgb(178, 182, 160);
  --wkd-white: rgb(255, 255, 255);
  --wkd-red: rgb(255, 0, 0);
  --wkd-green: rgb(82, 150, 34);
  --wkd-intense-blue: rgb(0, 0, 250);
  --wkd-pastell-blue: rgb(25, 145, 210);
  --wkd-torquise: rgb(8, 253, 216);
  --wkd-blueviolett: rgb(21, 23, 76);
  --wkd-yellow: rgb(255, 255, 0);
  --wkd-yellow-dark: rgb(255, 183, 0);
  --wkd-orange: rgb(255, 102, 0); /* filter: brightness(0) saturate(100%) invert(44%) sepia(24%) saturate(4068%) hue-rotate(1deg) brightness(98%) contrast(106%); */
  --wkd-grapefruit: rgb(239, 95, 87);
  --wkd-pink: rgb(255, 0, 128);
  --wkd-pinkviolett: rgb(255, 0, 255);  /* filter: brightness(0) saturate(100%) invert(19%) sepia(63%) saturate(4179%) hue-rotate(293deg) brightness(123%) contrast(136%); */
  --wkd-darkviolett: rgb(102, 0, 102); 
  --wkd-manner: rgb(235, 184, 173);
}

:root.theme_prime{

/*? === design === */
--main-colour: var(--wkd-darkgrey);
--counter-colour: var(--wkd-orange);
--design-strokes-colour: var(--wkd-grey);
--design-strokes-accent-colour: var(--wkd-pinkviolett);
--shadows: var(--wkd-white);

/*? === text === */
--text-optimal-width: clamp(45ch, 50%, 75ch);
--text-main-colour: var(--wkd-white);
--link-hover-color: var(--wkd-red);
}

/* #endregion */

/* #region resets */

/* box-sizing */
*,
*::before,
*::after { /* Safari/Chrome, other WebKit */ /* Firefox, other Gecko */
  box-sizing: outline-box; /* Opera/IE 8+ */
  margin: 0; /* margins */
}

/* set up the body*/
body {
  min-height: 100vh;
  line-height: 1.5;
}

/* make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* make form elements easier to work with */
input,
button,
textarea,
select {
  font: inherit;
}

/* remove animations for people who turned them off*/
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* #endregion */

/* #endregion */

.App {
    display: flex;
    flex-direction: column;
    grid-gap: .5rem;
    gap: .5rem;
    background-color: rgb(107, 106, 106);
    background-color: var(--wkd-grey);
}

.header{
  display: flex;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: .5rem;
  padding-bottom: .5rem;
}

.main-title-div{
  width: 30%;
  text-align: center;
  padding-left: 1rem;
  padding-right: 1rem;
}

.main-title-div > h1 {
  color: rgb(239, 95, 87);
  color: var(--wkd-grapefruit);
  font-size: medium;
  font-weight: bolder;
}

.main-title-div > h6 {
  color: rgb(255, 255, 255);
  color: var(--wkd-white);
}

.stats {
  display: flex;
  flex-direction: column;
  min-width: clamp(11rem, 13rem, auto);
  margin-left: auto;
  margin-right: 1rem;
  padding: .5rem;
  outline: dashed 2px rgb(25, 145, 210);
  outline: dashed 2px var(--wkd-pastell-blue);
  outline-width: 3px;
}

.header-points{
  display: flex;
  grid-gap: .5rem;
  gap: .5rem;
  text-align: center;
  color: rgb(255, 255, 255);
  color: var(--wkd-white);
  align-self: center;
}

.timeWrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.start-stop-reset{
  display: flex;
  width: 100%;
}
.startstop-btn {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 6ch;
  font-size: small;
  outline:none;
  border: none;
  color: rgb(255, 255, 255);
  color: var(--wkd-white);
  background-color: rgb(239, 95, 87);
  background-color: var(--wkd-grapefruit);
}

.reset-btn {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: small;
  margin-left: auto;
  outline:none;
  border: none;
  color: rgb(255, 255, 255);
  color: var(--wkd-white);
  background-color: rgb(25, 145, 210);
  background-color: var(--wkd-pastell-blue);
}

.cardboard {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: .5rem;
}

/* ? Transition & Hover */

.cards,
.reset-btn {
  transition: .1s ease-in-out ;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}

.cards:hover,
.reset-btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.cards:hover {
  outline: solid 3px  rgb(239, 95, 87);
  outline: solid 3px  var(--wkd-grapefruit);
}
.cards {
    width: calc( 20% - 1rem );
    height: 30vh;
    text-align: center;
    color: var(--wkd-white);
    background-color: var(--wkd-yellow-dark);
  }
  
  .cards-images{
    height: 100%;
    width: 100%;
  }
  
.stopwatch{
    width: 100%;
    display: flex;
    justify-content: center;
}

.numbers{
    display: flex;
    color: white;
}
.best-time-div{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    padding: .5rem;
    background-color: var(--wkd-pastell-blue);
}

.best-time-div > h3{
    font-size: x-small;
    color: white;
  }
  
  .best-time-div > p{
    font-size: x-small;
    color: white;
  }
